nav.navbar {
  margin-bottom: 0;
  background-color: rgb(235, 56, 12) !important;
  color: white;
}

.jumbotron {
  min-height: 300px;
  padding-top: 100px;
}

.card {
    width: 150px;
    height: 150px;
    margin: 1rem;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    transition: all .2s ease-in-out;
    float: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
